* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-family: Monument Extended, sans-serif;
}

body {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab) 0 0 / 400% 400%;
  animation: 15s infinite gradient;
  overflow-x: hidden;
}

header {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

header.minimized {
  height: 20vh;
  transition: all 1s cubic-bezier(.075, .82, .165, 1);
}

header input {
  color: #000;
  text-transform: uppercase;
  background-color: #fff3;
  border: none;
  border-radius: 100px;
  width: 8ch;
  padding: 3rem;
  font-family: Monument Extended, sans-serif;
  font-size: 2rem;
  font-weight: bold;
}

header input:focus {
  outline: none;
}

header input::placeholder {
  color: #fff3;
}

main {
  box-shadow: var(--shadow-elevation-medium);
  width: 100%;
  height: 0;
  display: flex;
  overflow: hidden;
}

header.minimized + main {
  height: 80vh;
}

main > * {
  flex-grow: 1;
  flex-basis: 1px;
  width: 100%;
  transition: all 1s cubic-bezier(.075, .82, .165, 1);
  position: relative;
}

main > :hover {
  flex-basis: 240px;
  transition: all 1s cubic-bezier(.075, .82, .165, 1);
}

main > :hover p {
  opacity: 1;
}

.color p {
  opacity: 0;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
  margin-top: -7px;
  font-size: 1rem;
  transition: all .1s cubic-bezier(.075, .82, .165, 1);
  display: block;
  position: absolute;
  top: 50%;
}

